/* =========== CUSTOM CONTENT FULLSCREEN =========== */
div#_pi_surveyWidgetCustom,
div#_pi_surveyWidgetContainer.mobile-enabled div#_pi_surveyWidgetCustom {
	height: calc(100% - 95px) !important;
	top: 95px !important;
}






/* =========== ALL WIDGET TYPES | HIDING BY VIEWPORT WIDTH =========== */
@media (min-width:769px) {
	#tablet-div { display:none; }
	#mobile-div { display:none; }
	body { background-color: #999 !important; }
}

@media (min-width:415px) and (max-width:768px) {
	#desktop-div { display:none; }
	#mobile-div { display:none; }
	body { background-color: #999 !important; }
}

@media (max-width:414px) {
	#desktop-div { display:none; }
	#tablet-div { display:none; }
	body { background-color: #999 !important; }
}